import stringMethods from "../library/stringMethods";

const DefaultClientFields = ({ fields }: any) => {
  const fieldNames = fields
    .filter(({ fieldPermanent }: any) => fieldPermanent === true)
    .map((e: any) => {
      const fieldName = new stringMethods(e.fieldName)
        .camelStringToText()
        .firstCharUpperCase()
        .getString();

      return <div key={e._id}>{fieldName}</div>;
    });

  return (
    <div>
      <h2>Default client fields</h2>
      <div>{fieldNames}</div>
    </div>
  );
};

export default DefaultClientFields;
